    <div class="search">
      <myHeaderSearch></myHeaderSearch>
    </div>

    <!-- model-->
    <mt-tab-container v-model="active">
      <transition name="tab-item">
        <mt-tab-container-item id="movie" key="movie">
          <div class="cityAndShow cf">
            <div class="changeh">
              <h2 @click="evalue_home" class="h2" id="home"></h2>
              <h2 @click="evalue_film" class="h2" id="film"></h2>
              <h2 @click="evalue_watchTV" class="h2" id="watchTV"></h2>
              <h2 @click="evaluate_rank" class="h2" id="evarank"></h2>
            </div>
          </div>
          <!--  -->
          <van-swipe :autoplay="3000">
            <van-swipe-item
              v-for="(item, i) of list"
              :key="i"
              class="carouselImg"
            >
              <img :src="`http://127.0.0.1:5050/${item.img_url}`" alt="" />
            </van-swipe-item> </van-swipe
          >

          <mt-tab-container v-model="activeTwo">
            <mt-tab-container-item id="home">
              <!--  -->
              <movie-list :is_show="0"></movie-list>
            </mt-tab-container-item>

