<template>
    <div class="app-login">
        <!-- 1 -->
        <div class="imgstyle">
            <img :src="imgurl" >
        </div>
        <div class="am-list">
            <!-- 2  -->
            <mt-field :placeholder="unameholder" v-model="uphone" class="am-list-item"></mt-field>
            <!-- 3 -->
            <mt-field :placeholder="upwdholder" v-model="upwd" class="am-list-item"></mt-field>
            <!-- 4 -->
            <div class="btnstyle">
                <mt-button @click="login"> </mt-button>
            </div>
            <!-- 5: -->
            <router-link to="/reguser" class="fontstyle">?</router-link>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            imgurl:"http://127.0.0.1:5050/01.png",
            unameholder:"",
            upwdholder:"",
            uphone:"",
            upwd:""
        }
    },
    // 
    beforeRouteEnter (to, from, next) {
        next(vm=>{
            console.log("",from.fullPath);
            // /reguser false
            if(from.path!="/reguser"){
                vm.$store.commit("changeToLoginPath",from.fullPath)
            }
            next();  //      
        })
    },
    computed:{
        toLoginPath(){ return this.$store.state.toLoginPath;},  //
        cid(){return this.$store.state.cid; },
        mid(){return this.$store.state.mid; },
    },
    methods:{
        login(){
            //
            //1
            var u=this.uphone;
            //2
            var p=this.upwd;
            // console.log(u+":"+p)
            //3:
            var ureg=/^1[3-9][0-9]{9}$/;
            var preg=/^\w{6}$/;
            // 4
            if(!ureg.test(u)){
                this.$toast("");
                return;
            }
            // 5
            if(!preg.test(p)){
                this.$toast("");
                return;
            }
            // 6ajax axios
            var url="user/v1/login";
            var obj={uphone:u,upwd:p}
            this.axios.get(url,{params:obj}).then(res=>{
                // 7
                // console.log(res);
                // 7.1    17816899467
                if(res.data.code==-1){
                    console.log(1);
                    this.$toast("")
                }else{
                    //uid;
                    this.$store.commit("changeUid",res.data.data);
                    //  /  /findCinema  /cinema 
                        var path=this.$store.getters.getToLoginPath;
                        //1 /
                        if(path=="/"){
                            //  active=movie  
                            // window.history.go(0); 
                            //vuex 
                            this.$store.commit("changeActive",'movie');  
                            // this.$router.push("/")     
                            this.$router.go(-1);
                        }else{
                            // this.$router.push(path);
                            this.$router.go(-1);         //history
                            // location.replace(path);   //vuex

                        }
                    }
                }
            )
        }
    }
}
</script>
