<template>
  <div id="movie_detail">
    <!-- 1 video -->
    <div class="detail_video" :style="{height:videoHeight+'px'}">
      <video :src="videoUrl" controls ></video>
      <div class="video_bottom"></div>
    </div>
    <!-- 2  -->
    <div class="detail_middle">
      <!--  -->
      <div class="middle_one cf">
        <!--  -->
        <div class="one_left">
          <img :src="picUrl" alt="">
          <div class="icon">{{list.pic_label}}</div>
        </div>
        <!--  -->
        <div class="one_right">
          <div class="right_1">{{list.mname}}</div>
          <div class="right_2">{{list.eng_name}}</div>
          <div class="right_3">{{list.label}}</div>
          <div class="right_4">{{list.show_time}} </div>
          <div class="right_5" :class="{orange:!is_show}">{{list.wish}}</div>
        </div>
      </div>
       <!--  -->     
      <div class="middle_two" :class="{'d-none':!is_show}">
        <div class="two_left">
          <div>{{list.score}}</div>
          <div class="start">
            <div class="start_inner" :style="{width:start_width}"></div>
          </div>
          <div></div>
          <div>{{list.score_num}}</div>

          <div></div>
        </div>
        <div class="two_right">
          <img src="../../../public/img/score_scale1.png" alt="">
        </div>
      </div>
    </div>
    <div class="detail_blank"></div>
    <!-- 3  -->
    <div class="detail_bottom">
      <mt-navbar v-model="selected">
        <mt-tab-item id="1"></mt-tab-item>
        <mt-tab-item id="2"></mt-tab-item>
        <mt-tab-item id="3"></mt-tab-item>
      </mt-navbar>
      <!-- tab-container -->
      <mt-tab-container v-model="selected">
        <mt-tab-container-item id="1">
         <div class="intro" :class="{height:isHeight}">
           {{list.intro}}
            <div class="show" @click="show"></div>
          </div> 
        </mt-tab-container-item>
        <mt-tab-container-item id="2">
         <div class="film"></div> 
        </mt-tab-container-item>
        <mt-tab-container-item id="3">
         <div class="more"></div> 
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
    <!-- 4  -->
    <h3 class="detail_seat" @click="find_cinema" v-show="path!='/cinema'">
      
    </h3>
  </div>  
</template>
<script>
// import movieLi from "./MovieLi";
export default {
  data(){
    return {
      path:'',    ///cinema 
      list:[],
      videoHeight:window.innerHeight*0.35,
      start_width:'0px',
      selected:"1",
      isHeight:true,  //
    }
  },
  // 
  beforeRouteEnter (to, from, next) {
    next(vm=>{
      console.log(from.path);
      console.log(vm);
      vm.path=from.path
      next();  //      
    })
  },
  methods:{
    //find_cinema
    find_cinema(){
      this.$router.push({
        path:'/findCinema',
        query:{mid:this.list.mid}
      })
    },
    //show 
    show(){
      this.isHeight=false;
      var show=document.getElementsByClassName("show")[0];
      console.log(show);
      show.style.display='none';
    },
    //start
    get_start_width(){
      var num=parseFloat(this.list.score);
      var width=num/10*80;
      this.start_width = width+'px';
    },
    //axios
    load(){
      var url="movie/v1/movie_detail";
      var obj={mid:this.mid};
      this.axios.get(url,{params:obj}).then(res=>{
        if(res.data.code==0){
          console.log("")
        }else{
          // list
          this.list=res.data.data[0];
          console.log(this.list);
          //
          //list
          this.get_start_width();
        }
      })
    },
  },
  created(){
    this.load();
  },
  computed:{
    url(){
      return this.$store.state.url;
    },
    //pic
    picUrl(){
      if(this.list.length==0){
        return 'http://127.0.0.1:5050/img/nezha.mp4';
        //   return null
      }else{ 
        return this.url+this.list.pic;
      }
    },
    //video
    videoUrl(){
      if(this.list.length==0){
        return 'http://127.0.0.1:5050/img/nezha.webp';
      }else{ 
        return this.url+this.list.video;
      }
    },
    is_show(){    //
      return (this.list.is_show==1) ? true : false;
    }
  },
  props:{
    mid:{default:""},
  },
}
</script>
<style>
    @import url('../../assets/css/movieDetail.css');
</style>