<script>
export default {
    data(){
        return{
            show:false, //
            time:"",
            cname:"",
            buyList:[],
            detail:{},
        }
    },
    computed:{
        url(){
            return this.$store.state.url
        },    
    },
    created(){
        this.getData();
        this.getTime();
    },
    methods:{
        //
        paymoney(){
            var mname=this.detail.cname;
            var tdate=`${this.time} ${this.detail.time_start}~${this.detail.time_end}`
            var d23=this.detail.d23;
            var cname=this.cname;
            var seat=[];
            //
            for (var i=0;i<this.buyList.length;i++){  
                seat.push(`${this.buyList[i].j+1}${this.buyList[i].i+1}`)
            }
            console.log(seat);
            var obj={ mname,tdate,d23,cname,seat };
            // return ;
            this.axios({
                method:"post",
                 url:"user/v1/ticket",
                 data:obj
            }).then(res=>{
                if(res.data.code==1){
                    console.log("")
                    //ticket
                    this.$router.push("/myticket")
                }else{
                    console.log("")
                }
            })
        },
        showPopup(){    //
            this.show=true;
        },
        //
        getData(){
            // var detail=this.$route.query;
            var obj=this.$store.getters.getSeatObj;
            // console.log(detail)
            this.cname=obj.cname.cname;
            this.buyList=obj.buyList;
            this.detail=obj.detail;  
            console.log(this.buyList)                      
        },
        //
        getTime(){
            var d=new Date();
            var month=d.getMonth();
            if(month+1<10){
                month++;
                month="0"+month
            }
            var date=d.getDate();
            if(date<10){
                date="0"+date;
            }
            this.time= `${month}-${date}`
        }
    }
    


}
</script>
